import React, { Component } from 'react'
import Loader from '../Components/loader'
export default class ImageCom extends Component {

    constructor(props) {
        super(props);
        this.state = {
          item: props,
          isLoaded: false,
        };
      }

    componentDidMount() {
        this.setState({
            loaded: false,
            error: false
            });
        const img = new Image();
        img.onload = () => {
            this.setState({
            loaded: true
            });
        };
        img.onerror = () => {
            this.setState({
            error: true
            });
        };
        img.src = this.props.src;
    }

    render() {
        if (this.state.error) {
          return <img
            className={this.props.className}
            style={this.props.style}
            src={this.props.unloadedSrc}
            alt={this.props.alt} />
        } else if (!this.state.loaded) {
          return <Loader></Loader>
        }
        return <img
          src={this.props.src} />
      }
}
